import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    keywords: "",
    resultData: []
  },
  getters: {},
  mutations: {
    setKeywords(state, payload) {
      state.keywords = payload.keywords;
    },
    setResultData(state, payload) {
      state.resultData = payload.resultData;
    }
  },
  actions: {},
  modules: {}
});
export default store;
